
































import Vue from 'vue';
import { Prop, Component } from 'vue-property-decorator';

@Component
export default class Button extends Vue {
    @Prop()
    title: string;
    @Prop({default: ''})
    className: string;

    onClick() {
        this.$emit('click');
    }
}



